.Home1{
    width: 100%;
    height: 100vh;
    background-color: rgb(3, 22, 54);
    >main{
        height: 100%;
        width: 100%;
        position: absolute;
        padding: 1rem;
        display: flex;
        justify-content: flex-end;
        
        >h1{
            margin-right: 4rem;
            font-size: 50px;
            font-family:cursive;
           margin-top: 15%;
           animation: imgUp 0.8s linear infinite alternate;
        }


    >h2{
        color: azure;
    }
    >h1{
        color: azure;
    }
    >p{
        color: azure;
    }}
    &::before{
    content: "";
    background: no-repeat center url("../assets/techimage2.jpg");
    position: absolute;
    width: 100%;
    height: 100%;
    background-size: cover;
    border-radius: 0 0 200px 200px;
    filter: blur(2px);
    
   

}
}
.Home2{
   
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    background-color:  rgb(3, 22, 54);
    
    h1{
        color: azure;
    }
    p{
        color: azure;
    }
   > img{
    width:20% ;
    animation: imgUp 0.7s linear infinite alternate;
    border-radius: 100px 100px 100px 100px;
    }
    >div{
        height: 100vh;
        width: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        padding: 100px;
        height: 100vh;
        >p{
            font-size:x-large;
            font-weight: 300;
        }
    }
}
@keyframes imgUp {
    to {
      transform: translateY(-20px);
    }
  }
  .Home3{
    height: 100vh;
    width: 100%;
    background-color:  rgb(3, 22, 54);
    display: flex;
    flex-direction: column;
    
    >div{
        
        background-color: aliceblue;
        width: 70%;
        height: 90vh;
        border-radius: 0 200px 200px 0;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        >h1{
            font-family:cursive;
            font-size: 70px;
            font-weight: 300;
            margin-top: 10px;
            color: brown;
            animation:imgUp 0.7s linear infinite alternate;
        }
        >p{
            font: 200;
            font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
            font-weight: 400;
            text-decoration:none;
            padding: 5rem;  
            letter-spacing: 1px;
            word-spacing: 5px;      
        }
    }
  }
  .Home4{
    height:100vh ;
    width: 90%;
    padding: 10rem;
    background-color: rgb(3, 22, 54);
    display: flex;
    justify-content: flex-end;
   > div{
        background-color: rgb(164, 27, 243);
        width: 60%;
        height: 40vh;
        padding: 4rem;
        display: flex;
        border-radius :200px 0 0 200px ;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        >h1{
            padding: 0.5rem;
            border-bottom:2px solid white;
            color: azure;
            font-family:Georgia, 'Times New Roman', Times, serif
        }
        >article{
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 3rem;
            >div{
                margin: 1rem;
                width: 8rem;
                height:8rem;
                animation: imgUp 0.7s linear infinite alternate;
                transition: all 0.5s;
                background-color: aliceblue;
                border-radius: 200px 200px 200px 200px;
                display: flex;
                justify-content: center;
                flex-direction: column;
                align-items: center;
                
                
                &:hover {
                    filter: invert(1);
                  }
                svg {
                    font-size: 5rem;
                    cursor: pointer;
                  }
                  p{
                    font-size: large;
                    color: rgb(1, 1, 1);
                    font-family: cursive;
                  }
                
            }
        }
   }
    > h1 {
        color: whitesmoke;
        text-transform: uppercase;
        width: 140px;
        border-bottom: 2px white;
        padding: 0.5rem;
      }
  }