.contact{
    height: 100vh;
    width: 100%;
    background-color: rgb(3, 22, 54);
    display: flex;
    justify-content: center;
    >div{
        height: 15rem;
        width: 30rem;
        background-color: aliceblue;
        padding: 4rem;
        margin: 8rem;
        border-radius:  200px 0 200px 0;
        display: flex;
        justify-content: center;
        flex-direction: column;
       font-size: larger;
       font-family: cursive;
       animation: imgUp 0.7s linear infinite alternate;
       >p{
        word-spacing: 0.3rem;
        letter-spacing: 1px;
       }
    }

}
@keyframes imgUp {
    to {
      transform: translateY(-20px);
    }
  }