.footer{
    display: flex;
    margin: 2rem;
    justify-content: space-between;
    font-family: cursive;
    
}
.sociallink{
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin: 5rem;
    >h1{
        font-size: xx-large;
        border-bottom: 2px solid black;
        font-family: cursive;
    }
    >a{
        font-weight: 400;
        font-size: x-large;
        font-family: cursive;
        margin-top: 0.3rem;
    }
    >div{
        margin-top: 3rem;
    }
}