
nav{
    width: 100%;
    height: 6rem;
    background-color: rgb(255, 255, 255);
    position: sticky;
    display: flex;
    justify-content: space-between;
    align-items: center;
    top: 0%;
    z-index: 10;
   
    
    >h1{
        color: rgb(0, 0, 0);
        justify-content: center;
        margin-left: 1rem;
        font-weight: 400;
        font-family: cursive;

    }
    >main{
        
        width: 70%;
        display: flex;
        justify-content: flex-end;
        margin-right: 4rem;
        >a{
            margin: 2rem;
            color: brown;
            font-family:cursive;
            font-size: x-large;
            font-weight: 300;
        }
    }
   
}

